body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.App {
  text-align: center;
  position: absolute;
  overflow-x: hidden;
  overflow-anchor: none;
  height: 100vh;
  width: 100vw;
  font-family: 'Ubuntu', sans-serif;
  background-color: rgba(0, 0, 0, 0.05);
}

body {
  overflow-x: hidden;
  margin-bottom: 10px;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
  margin: 10px 0 10px 0;
}

.App-header > h1 {
  font-size: calc(20px + 2vmin);
  font-weight: 700;
}

.App-main {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 90vh;
  width: 100vw;
}

.App-subhead {
  width: 100%;
  color: rgb(0, 0, 0, 0.7);
  font-size: 15px;
}

.App-preview {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

.Preview-main {
  border: 1px solid rgb(131, 131, 131);
  border-radius: 5px;
  position: absolute;
  height: 1122px;
  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.input-div {
  text-align: right;
  width: 350px;
}

.Preview-name-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 100px;
  margin-right: 0;
  margin-left: 300px;
  height: 200px;
  width: 500px;
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
  color: white;
}

#Preview-first-last {
  width: auto;
  height: 60px;
  font-size: 50px;
  font-weight: 500;
}

#Preview-job-title {
  width: 100%;
  font-size: 25px;
}

.Preview-contact-skills-pane {
  position: absolute;
  height: 1122px;
  width: 250px;
  background-color: #271f44;
  justify-content: left;
}

.Preview-picture {
  display: flex;
  position: relative;
  background-color: white;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  margin-top: 100px;
  margin-left: 25px;
  margin-bottom: 55px;
  align-items: center;
  justify-content: center;
}

.Preview-profile-picture {
  display: flex;
  background-color: white;
  align-items: center;
  justify-content: center;
  max-width: 50%;
  max-height: 50%;
  object-fit: cover;
  border-radius: 50%;
}

#image-upload {
  display: none;
}

.upload-label {
  padding: 7px 15px;
  cursor: pointer;
  border-radius: 5px;
  color: #ffffff;
}

.Preview-panel-list {
  display: inline-block;
  color: white;
  width: 100px;
  justify-content: left;
  align-items: flex-start;
  text-align: left;
  width: 80%;
  min-height: 40px;
}

li {
  position: relative;
  left: 20px;
  margin-bottom: 10px;
  padding-left: 10px;
}

.Open-modal-button-div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Preview-panel-div {
  color: white;
  margin-bottom: 25px;
}

.Preview-contact-div {
  display: flex;
  align-items: center;
  text-align: left;
  word-break: break-word;
  color: white;
  margin-left: 20px;
  grid-gap: 10px;
  gap: 10px;
  width: 80%;
  height: 70px;
}

.Preview-contact-div > * > h3 {
  line-height: 25px;
  font-size: 16px;
  font-weight: 900;
}

.Preview-panel-headline {
  width: 250px;
  height: 30px;
  border-bottom: 4px solid white;
  margin-bottom: 20px;
  line-height: 20px;
}

.Preview-icon {
  height: 35px;
  width: 35px;
}

.Preview-experience-main {
  margin-top: 64.5px;
  margin-left: 250px;
}

.Preview-experience-header {
  text-align: left;
  padding-left: 8%;
  border-bottom: 4px solid black;
  height: 21px;
  line-height: 0px;
}
/* 
accent colors */
.Preview-contact-div > * > h3,
.App-link {
  color: rgb(111, 111, 255);
}

.Preview-name-title {
  background-color: rgb(111, 111, 255);
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 20px;
  font-weight: 900;
}

li {
  list-style-type: circle;
  list-style: url(data:image/png;base64,);
  padding-right: 15px;
}

ul {
  margin-left: 10px;
}

.Preview-education-entry,
.Preview-experience-entry {
  display: flex;
  text-align: left;
  flex-direction: row;
  align-items: center;
  height: 50px;
  grid-gap: 40px;
  gap: 40px;
  margin-left: 45px;
  margin-top: 30px;
}

.Preview-education-year,
.Preview-experience-year {
  border-right: 3px solid black;
  width: 110px;
  height: 100%;
  align-items: center;
  display: flex;
}

.Preview-education-school,
.Preview-experience-job {
  display: flex;
  flex-direction: column;
}

.Preview-education-main {
  margin-top: 64.5px;
  margin-left: 250px;
}

.Preview-education-header {
  text-align: left;
  padding-left: 8%;
  border-bottom: 4px solid black;
  height: 21px;
  line-height: 0px;
}

.Preview-contact-div-headline {
  pointer-events: none;
}

#preview-button {
  width: 130px;
  height: 40px;
  margin: 10px 0 10px 0;
  font-size: 16px;
}

#preview-button,
.upload-label,
.open-modal-button {
  color: white;
  font-size: 16px;
  background-color: rgb(131, 131, 131);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-weight: 700;
}

.upload-label,
#image-upload {
  position: absolute;
  top: 40%;
  left: 39%;
}

#preview-button:hover,
.upload-label:hover,
.open-modal-button:hover {
  background-color: rgba(170, 170, 170, 0.7);
  color: rgb(49, 49, 49);
}

.open-modal-button {
  display: flex;
  display: inline-block;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
}

.open-modal-button,
.upload-label {
  font-size: 30px;
}

